/* Synerity UI Component Styles */
/* Button Component Styles */
.sui-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  transition: all 0.2s ease-in-out;
  border-radius: 0.375rem;
  border: 1px solid transparent;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  position: relative;
  overflow: hidden;
}

.sui-button:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.sui-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}

/* Button Sizes */
.sui-button-sm {
  padding: 0.375rem 0.75rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.sui-button-md {
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.sui-button-lg {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  line-height: 1.5rem;
}

/* Button Variants - Using new theme system */
button.sui-button.sui-button-primary,
.sui-button.sui-button-primary {
  background-color: var(--sui-primary-600);
  color: white;
  border-color: var(--sui-primary-600);
}

button.sui-button.sui-button-primary:hover:not(:disabled),
.sui-button.sui-button-primary:hover:not(:disabled) {
  background-color: var(--sui-primary-700);
  border-color: var(--sui-primary-700);
}

.sui-button-primary:focus {
  box-shadow: 0 0 0 2px var(--sui-primary-500);
  outline: 2px solid transparent;
  outline-offset: 2px;
}

button.sui-button.sui-button-secondary,
.sui-button.sui-button-secondary {
  background-color: var(--sui-secondary-600);
  color: white;
  border-color: var(--sui-secondary-600);
}

button.sui-button.sui-button-secondary:hover:not(:disabled),
.sui-button.sui-button-secondary:hover:not(:disabled) {
  background-color: var(--sui-secondary-700);
  border-color: var(--sui-secondary-700);
}

button.sui-button.sui-button-outline,
.sui-button.sui-button-outline {
  background-color: transparent;
  color: var(--sui-gray-700);
  border-color: var(--sui-gray-300);
}

button.sui-button.sui-button-outline:hover:not(:disabled),
.sui-button.sui-button-outline:hover:not(:disabled) {
  background-color: var(--sui-gray-50);
  border-color: var(--sui-gray-400);
}

button.sui-button.sui-button-ghost,
.sui-button.sui-button-ghost {
  background-color: transparent;
  color: var(--sui-gray-700);
  border-color: transparent;
}

button.sui-button.sui-button-ghost:hover:not(:disabled),
.sui-button.sui-button-ghost:hover:not(:disabled) {
  background-color: var(--sui-gray-100);
}

button.sui-button.sui-button-danger,
.sui-button.sui-button-danger {
  background-color: var(--sui-red-600);
  color: white;
  border-color: var(--sui-red-600);
}

button.sui-button.sui-button-danger:hover:not(:disabled),
.sui-button.sui-button-danger:hover:not(:disabled) {
  background-color: var(--sui-red-700);
  border-color: var(--sui-red-700);
}

/* Full Width */
.sui-button-full-width {
  width: 100%;
}

/* Loading State */
.sui-button-loading {
  position: relative;
  color: transparent;
}

.sui-button-loading::after {
  content: "";
  position: absolute;
  width: 1rem;
  height: 1rem;
  top: 50%;
  left: 50%;
  margin-left: -0.5rem;
  margin-top: -0.5rem;
  border: 2px solid transparent;
  border-top-color: currentColor;
  border-radius: 50%;
  animation: sui-button-spin 1s linear infinite;
}

@keyframes sui-button-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* Button Additional Styles */
.sui-button-spinner {
  animation: sui-button-spin 1s linear infinite;
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
}

.sui-button-icon-left {
  margin-right: 0.5rem;
}

.sui-button-icon-right {
  margin-left: 0.5rem;
}

/* Badge Component Styles */
.sui-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  border-radius: 9999px;
  white-space: nowrap;
  vertical-align: middle;
  line-height: 1;
}

/* Badge Sizes */
.sui-badge-small {
  padding: 0.125rem 0.375rem;
  font-size: 0.75rem;
  line-height: 1rem;
}

.sui-badge-normal {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.sui-badge-large {
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5rem;
}

/* Badge Severity Variants */
.sui-badge-info {
  background-color: #dbeafe;
  color: #1e40af;
}

.sui-badge-success {
  background-color: #dcfce7;
