}

/* Badge with no value (dot indicator) */
.sui-badge-dot {
  width: 0.5rem;
  height: 0.5rem;
  padding: 0;
  border-radius: 50%;
}

.sui-badge-dot.sui-badge-small {
  width: 0.375rem;
  height: 0.375rem;
}

.sui-badge-dot.sui-badge-large {
  width: 0.625rem;
