}

/* Avatar Component Styles */
.sui-avatar {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  color: #ffffff;
  background-color: #6b7280;
  border: 2px solid #ffffff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  position: relative;
  overflow: hidden;
}

/* Avatar Sizes */
.sui-avatar-small {
  width: 2rem;
  height: 2rem;
  font-size: 0.75rem;
  line-height: 1rem;
}

.sui-avatar-normal {
  width: 2.5rem;
  height: 2.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.sui-avatar-large {
  width: 3rem;
  height: 3rem;
  font-size: 1rem;
  line-height: 1.5rem;
}

.sui-avatar-xlarge {
  width: 4rem;
  height: 4rem;
  font-size: 1.25rem;
  line-height: 1.75rem;
}

/* Avatar Shapes */
.sui-avatar-circle {
  border-radius: 50%;
}

.sui-avatar-square {
  border-radius: 0.375rem;
}

/* Avatar Image */
.sui-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: inherit;
}

/* Progress Bar Component Styles */
.sui-progress-bar {
  position: relative;
  display: block;
  width: 100%;
  height: 0.5rem;
  background-color: #e5e7eb;
  border-radius: 9999px;
  overflow: hidden;
}

.sui-progress-bar-fill {
  height: 100%;
  background-color: #3b82f6;
  border-radius: inherit;
  transition: width 0.3s ease-in-out;
  position: relative;
  overflow: hidden;
}

/* Progress Bar Additional Styles */
.sui-progress-bar-track {
  width: 100%;
  height: 100%;
  background-color: #e5e7eb;
  border-radius: 9999px;
  overflow: hidden;
}

.sui-progress-bar-value {
  margin-top: 0.25rem;
  font-size: 0.75rem;
  color: #6b7280;
  text-align: center;
}

/* Input Text Component Styles */
.sui-input-text {
  position: relative;
  display: block;
  width: 100%;
}

.sui-input-text {
  width: 100%;
  border: 1px solid #d1d5db;
