}

/* Input Text Sizes */
.sui-input-text-small {
  padding: 0.375rem 0.5rem;
  font-size: 0.75rem;
  line-height: 1rem;
}

.sui-input-text-normal {
  padding: 0.5rem 0.75rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.sui-input-text-large {
  padding: 0.75rem 1rem;
