}

/* Input Text Additional Styles */
.sui-input-text-container {
  position: relative;
  display: block;
  width: 100%;
}

.sui-input-text-wrapper {
  position: relative;
  display: block;
}

.sui-input-text-prefix {
  position: absolute;
  left: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  color: #6b7280;
  pointer-events: none;
  z-index: 1;
}

.sui-input-text-suffix {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.sui-input-text-clear {
  background: none;
  border: none;
  color: #6b7280;
