/* Textarea Component Styles with CSS Variables */
.sui-textarea-container {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  gap: 0.5rem;
  width: 100%;
}

.sui-textarea-input {
  display: block;
  width: 100%;
  padding: 0.5rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  color: var(--sui-gray-900, #111827);
  background-color: var(--sui-white, #ffffff);
  border: 1px solid var(--sui-gray-300, #d1d5db);
  border-radius: 0.375rem;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  transition: all 0.2s ease-in-out;
  min-height: 2.5rem;
  resize: vertical;
  font-family: inherit;
}

.sui-textarea-input:focus {
  outline: none;
  border-color: var(--sui-primary-500, #3b82f6);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.sui-textarea-input:disabled {
  background-color: var(--sui-gray-100, #f3f4f6);
  color: var(--sui-gray-500, #6b7280);
  cursor: not-allowed;
  opacity: 0.75;
}

.sui-textarea-input::placeholder {
  color: var(--sui-gray-400, #9ca3af);
}

.sui-textarea-input:invalid {
  border-color: var(--sui-red-500, #ef4444);
}

.sui-textarea-input:invalid:focus {
  border-color: var(--sui-red-500, #ef4444);
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}

/* Tooltip Component Styles with CSS Variables */
.sui-tooltip-wrapper {
  display: inline-block;
  position: relative;
}

.sui-tooltip {
  position: absolute;
  z-index: 50;
  padding: 0.5rem 0.75rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: var(--sui-white, #ffffff);
  background-color: var(--sui-gray-900, #111827);
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  max-width: 20rem;
  word-wrap: break-word;
  opacity: 0;
  transform: scale(0.95);
  transition: all 0.15s ease-in-out;
  pointer-events: none;
}

.sui-tooltip.show {
  opacity: 1;
  transform: scale(1);
}

/* Position variants */
.sui-tooltip-top {
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(-8px) scale(0.95);
  margin-bottom: 8px;
}

.sui-tooltip-top.show {
  transform: translateX(-50%) translateY(0) scale(1);
}

.sui-tooltip-bottom {
  top: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(8px) scale(0.95);
  margin-top: 8px;
}

.sui-tooltip-bottom.show {
  transform: translateX(-50%) translateY(0) scale(1);
}

.sui-tooltip-left {
  right: 100%;
  top: 50%;
  transform: translateY(-50%) translateX(-8px) scale(0.95);
  margin-right: 8px;
}

.sui-tooltip-left.show {
  transform: translateY(-50%) translateX(0) scale(1);
}

.sui-tooltip-right {
  left: 100%;
  top: 50%;
  transform: translateY(-50%) translateX(8px) scale(0.95);
  margin-left: 8px;
}

.sui-tooltip-right.show {
  transform: translateY(-50%) translateX(0) scale(1);
}

/* Arrow styles */
.sui-tooltip-arrow {
  position: absolute;
  width: 8px;
  height: 8px;
  background-color: var(--sui-gray-900, #111827);
  transform: rotate(45deg);
}

.sui-tooltip-top .sui-tooltip-arrow {
  bottom: -4px;
  left: 50%;
  transform: translateX(-50%) rotate(45deg);
}

.sui-tooltip-bottom .sui-tooltip-arrow {
  top: -4px;
  left: 50%;
  transform: translateX(-50%) rotate(45deg);
}

.sui-tooltip-left .sui-tooltip-arrow {
  right: -4px;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}

.sui-tooltip-right .sui-tooltip-arrow {
  left: -4px;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}

/* Content wrapper */
.sui-tooltip-content {
  position: relative;
  z-index: 1;
}

/* Theme variants */
.sui-tooltip-light {
  background-color: var(--sui-white, #ffffff);
  color: var(--sui-gray-900, #111827);
  border: 1px solid var(--sui-gray-200, #e5e7eb);
}

.sui-tooltip-light .sui-tooltip-arrow {
  background-color: var(--sui-white, #ffffff);
  border: 1px solid var(--sui-gray-200, #e5e7eb);
}

.sui-tooltip-primary {
  background-color: var(--sui-primary-600, #2563eb);
  color: var(--sui-white, #ffffff);
}

.sui-tooltip-primary .sui-tooltip-arrow {
  background-color: var(--sui-primary-600, #2563eb);
}

.sui-tooltip-success {
  background-color: var(--sui-green-600, #059669);
  color: var(--sui-white, #ffffff);
}

.sui-tooltip-success .sui-tooltip-arrow {
  background-color: var(--sui-green-600, #059669);
}

.sui-tooltip-warning {
  background-color: var(--sui-yellow-600, #d97706);
  color: var(--sui-white, #ffffff);
}

.sui-tooltip-warning .sui-tooltip-arrow {
  background-color: var(--sui-yellow-600, #d97706);
}

.sui-tooltip-error {
