  font-size: 1rem;
  max-width: 24rem;
}

/* Card Component Styles */
.sui-card {
  position: relative;
  display: block;
  background-color: var(--sui-white, #ffffff);
  border: 1px solid var(--sui-gray-200, #e5e7eb);
  border-radius: 0.5rem;
  padding: 1rem;
  transition: all 0.2s ease-in-out;
}

/* Card Sizes */
.sui-card-sm {
  padding: 1rem;
}

.sui-card-md {
  padding: 1.5rem;
}

.sui-card-lg {
  padding: 2rem;
}

/* Card Variants */
.sui-card-default {
  background-color: var(--sui-white, #ffffff);
  border: 1px solid var(--sui-gray-200, #e5e7eb);
}

.sui-card-outlined {
  background-color: transparent;
  border: 2px solid var(--sui-primary-200, #bfdbfe);
}

.sui-card-elevated {
  background-color: var(--sui-white, #ffffff);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border: 1px solid var(--sui-gray-200, #e5e7eb);
}

.sui-card-filled {
  background-color: var(--sui-primary-50, #eff6ff);
