  cursor: not-allowed;
}

.sui-card-borderless {
  border: none;
}

/* Card Loading */
.sui-card-loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.5rem;
  z-index: 10;
}

.sui-card-loading-spinner {
  animation: spin 1s linear infinite;
  width: 1.5rem;
  height: 1.5rem;
  color: var(--sui-primary-600, #2563eb);
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* Stats card hover effect */
.stats-card {
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
