.sui-checkbox.checkbox-secondary input[type=checkbox] {
  color: #6b7280 !important;
}

.sui-checkbox.checkbox-secondary input[type=checkbox]:focus {
  box-shadow: 0 0 0 3px rgba(107, 114, 128, 0.5);
}

.sui-checkbox.checkbox-secondary input[type=checkbox]:checked,
.sui-checkbox.checkbox-secondary input[type=checkbox]:indeterminate {
  background-color: #6b7280 !important;
  border-color: #6b7280 !important;
}

.sui-checkbox.checkbox-success input[type=checkbox] {
  color: #10b981 !important;
}

.sui-checkbox.checkbox-success input[type=checkbox]:focus {
  box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.5);
}

.sui-checkbox.checkbox-success input[type=checkbox]:checked,
.sui-checkbox.checkbox-success input[type=checkbox]:indeterminate {
  background-color: #10b981 !important;
  border-color: #10b981 !important;
}

.sui-checkbox.checkbox-warning input[type=checkbox] {
  color: #f59e0b !important;
}

.sui-checkbox.checkbox-warning input[type=checkbox]:focus {
  box-shadow: 0 0 0 3px rgba(245, 158, 11, 0.5);
}

.sui-checkbox.checkbox-warning input[type=checkbox]:checked,
.sui-checkbox.checkbox-warning input[type=checkbox]:indeterminate {
  background-color: #f59e0b !important;
  border-color: #f59e0b !important;
}

.sui-checkbox.checkbox-danger input[type=checkbox] {
  color: #ef4444 !important;
}

.sui-checkbox.checkbox-danger input[type=checkbox]:focus {
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.5);
}

.sui-checkbox.checkbox-danger input[type=checkbox]:checked,
.sui-checkbox.checkbox-danger input[type=checkbox]:indeterminate {
  background-color: #ef4444 !important;
