  padding: 8px 12px;
  background: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
  flex-wrap: wrap;
}
.sui-editor-toolbar-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border: none;
  background: transparent;
  border-radius: 4px;
  cursor: pointer;
  color: #6b7280;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.15s ease;
  position: relative;
}
.sui-editor-toolbar-button:hover {
  background-color: #e5e7eb;
  color: #374151;
}
.sui-editor-toolbar-button.active {
  background-color: #3b82f6;
  color: #ffffff;
}
.sui-editor-toolbar-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.sui-editor-toolbar-button svg {
  width: 16px;
  height: 16px;
}
.sui-editor-toolbar-separator {
  width: 1px;
  height: 24px;
  background-color: #e5e7eb;
  margin: 0 4px;
}
.sui-editor-toolbar-select {
  border: 1px solid #d1d5db;
  border-radius: 4px;
