}
.sui-confirm-popup-actions {
  @apply flex justify-end space-x-3;
}
.sui-confirm-popup-button {
  @apply px-3 py-2 text-sm font-medium transition-colors;
}
.sui-confirm-popup-button--reject {
  color: var(--sui-color-gray-700);
  border-color: var(--sui-color-gray-300);
  @apply bg-white border rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500;
}
.sui-confirm-popup-button--accept {
  background-color: var(--sui-color-primary-600);
  @apply text-white border border-transparent rounded-md hover:bg-primary-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500;
}
.sui-confirm-popup-arrow {
  border-color: var(--sui-color-gray-200);
  @apply absolute w-2 h-2 bg-white border transform rotate-45;
}
.sui-confirm-popup-arrow--top {
  @apply -bottom-1 left-1/2 transform -translate-x-1/2 border-t-0 border-l-0;
}
.sui-confirm-popup-arrow--bottom {
  @apply -top-1 left-1/2 transform -translate-x-1/2 border-b-0 border-r-0;
}
.sui-confirm-popup-arrow--left {
  @apply -right-1 top-1/2 transform -translate-y-1/2 border-l-0 border-b-0;
}
.sui-confirm-popup-arrow--right {
  @apply -left-1 top-1/2 transform -translate-y-1/2 border-r-0 border-t-0;
}
.sui-confirm-popup {
  /* Position variants */
}
.sui-confirm-popup--top .sui-confirm-popup-popup {
  @apply transform -translate-y-full;
}
.sui-confirm-popup--bottom .sui-confirm-popup-popup {
  @apply transform translate-y-full;
}
.sui-confirm-popup--left .sui-confirm-popup-popup {
  @apply transform -translate-x-full;
}
.sui-confirm-popup--right .sui-confirm-popup-popup {
  @apply transform translate-x-full;
}

/* Message Component Styles */
.sui-message {
  @apply flex items-center p-4 rounded-lg border transition-all duration-200;
}
.sui-message--success {
  @apply bg-green-50 border-green-200 text-green-800;
}
.sui-message--info {
  @apply bg-blue-50 border-blue-200 text-blue-800;
}
.sui-message--warn {
  @apply bg-yellow-50 border-yellow-200 text-yellow-800;
}
.sui-message--error {
  @apply bg-red-50 border-red-200 text-red-800;
}
.sui-message-content {
  @apply flex items-center w-full;
}
.sui-message-icon {
  @apply flex-shrink-0 mr-3 text-lg;
}
.sui-message-text {
  @apply flex-1 text-sm font-medium;
}
.sui-message-close {
  @apply flex-shrink-0 ml-3 text-lg font-bold hover:opacity-70 focus:outline-none focus:ring-2 focus:ring-offset-2 rounded;
}
.sui-message {
  /* Size variants */
}
.sui-message--small {
  @apply p-2 text-sm;
}
.sui-message--large {
  @apply p-6 text-lg;
}
.sui-message {
  /* Animation states */
}
.sui-message-enter {
  @apply opacity-0 transform scale-95;
}
.sui-message-enter-active {
  @apply opacity-100 transform scale-100 transition-all duration-200 ease-out;
}
.sui-message-exit {
  @apply opacity-100 transform scale-100;
}
.sui-message-exit-active {
  @apply opacity-0 transform scale-95 transition-all duration-200 ease-in;
}

/* Speed Dial Animations */
@keyframes speedDialSlideIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes speedDialFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes speedDialScaleIn {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
/* Animate On Scroll Animations */
@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-30px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translateX(30px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slideInUp {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes slideInDown {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes slideInLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes slideInRight {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes zoomOut {
  0% {
    opacity: 0;
    transform: scale(1.3);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes rotateIn {
  0% {
    opacity: 0;
    transform: rotate(-200deg);
  }
  100% {
    opacity: 1;
    transform: rotate(0deg);
  }
}
@keyframes flipInX {
  0% {
    opacity: 0;
    transform: perspective(400px) rotateX(90deg);
  }
  100% {
    opacity: 1;
    transform: perspective(400px) rotateX(0deg);
  }
}
@keyframes flipInY {
  0% {
    opacity: 0;
    transform: perspective(400px) rotateY(90deg);
  }
  100% {
    opacity: 1;
    transform: perspective(400px) rotateY(0deg);
  }
}
/* Split Button Animations */
@keyframes splitButtonSlideDown {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes splitButtonFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/* Inplace Animations */
@keyframes inplaceSlideIn {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes inplaceFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes inplaceScaleIn {
  0% {
    opacity: 0;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
/* Meter Group Animations */
@keyframes meterGroupSlideIn {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes meterGroupPulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}
@keyframes meterGroupBounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}
/* Tooltip Animations */
@keyframes tooltipFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes tooltipSlideIn {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes tooltipScaleIn {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
/* Captcha Animations */
@keyframes captchaShake {
  0%, 100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  75% {
    transform: translateX(5px);
  }
}
@keyframes captchaSuccess {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
/* Dynamic Dialog Animations */
@keyframes dynamicDialogFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes dynamicDialogSlideIn {
  0% {
    opacity: 0;
    transform: scale(0.95) translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
/* Block UI Animations */
@keyframes blockUiFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes blockUiSlideIn {
  0% {
    opacity: 0;
    transform: scale(0.95) translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
/* Drawer Animations */
@keyframes drawerSlideIn {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes drawerSlideOut {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
/* Overlay Panel Animations */
@keyframes overlayPanelFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes overlayPanelSlideIn {
  0% {
    opacity: 0;
    transform: scale(0.95) translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
/* Popover Animations */
@keyframes popoverFadeIn {
  0% {
    opacity: 0;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
/* Progress Spinner Animations */
@keyframes progressSpinnerRotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* Skeleton Animations */
@keyframes skeletonShimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
/* File Upload Animations */
@keyframes fileUploadPulse {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.02);
  }
}
@keyframes fileUploadSlideIn {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
/* Confirm Popup Animations */
@keyframes confirmPopupFadeIn {
  0% {
    opacity: 0;
    transform: scale(0.95) translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
/* Progress Bar Animations */
@keyframes progressIndeterminate {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}
@keyframes progressStriped {
  0% {
    background-position: 1rem 0;
  }
  100% {
    background-position: 0 0;
  }
}
/* Dialog Animations */
.sui-dialog-enter {
  @apply opacity-0 scale-95;
}

.sui-dialog-enter-active {
  @apply opacity-100 scale-100 transition-all duration-200;
}

.sui-dialog-exit {
  @apply opacity-100 scale-100;
}

.sui-dialog-exit-active {
  @apply opacity-0 scale-95 transition-all duration-200;
}

